import React from "react";
import homeHeader from '../../assests/homeHeader.png'
import homeHeader2 from '../../assests/homeHeader2.svg'

export default function Header(){
    return(
        <article className="d-flex flex-wrap">
            <section className="col-lg-6 col-12">
                <div className="mb-2">
                    <img src={homeHeader2} className="col-lg-6 col-6" />
                </div>
                <div>
                    <h1 className="home-header-main-title">Medical</h1>
                    <h1 className="header-sub-title">Laboratories</h1>
                    <p className="home-header-text">
                        Medical Laboratory Scientist are NOT Biochemists,
                        Microbiologist, or Science Laboratory Technologists.                        
                    </p>
                    <button className="header-register-btn my-5 py-3 px-5 header-register-btn-text">Register Laboratory</button>
                </div>
                <div></div>
            </section>
            <section className="col-lg-6 col-12">
                <img src={homeHeader} className="col-lg-12 col-12" />
            </section>
        </article>
    )
}