import React from "react";
import { Formik, Form, Field } from 'formik'
import ellipseRed from '../../assests/ellipseRed.svg'
import phoneWhite from '../../assests/phoneWhite.svg'

const options = [
    {
        state: "state 1",
        LGAs: ["LGA 1a", "LGA 1b", "LGA 1c", "LGA 1d"]
    },
    {
        state: "state 2",
        LGAs: ["LGA 2a", "LGA 2b", "LGA 2c", "LGA 2d"]
    }, 
    {
        state: "state 3",
        LGAs: ["LGA 3a", "LGA 3b", "LGA 3c", "LGA 3d"]
    }      
]

export default function HomeForm(){

    const statesDisplay = options.map((opt, i) => {
        const { state } = opt
        return <option value={state} key={i}>{state}</option>
    })

    const LGADisplay = chosenState => {
        if(chosenState){
            let lgas = []
            for(let i = 0; i < options.length; i++){
                const { state, LGAs } = options[i]
                if(state == chosenState){
                    for(let j = 0; j < LGAs.length; j++){
                        lgas.push(
                            <option value={LGAs[j]} key={j}>{LGAs[j]}</option>
                        )
                    }
                    break;
                }
            }
            return lgas
        }
    }

    return (
        <article>
            <div className="mb-5 section-horizontal-spacing-normal">
                <h1 className="home-form-title">
                    MLSCN <span className="home-form-title2">Approved Laboratories</span>
                </h1>
            </div>
            <Formik
                initialValues={{
                    state: "", LGA: ""
                }}
            >
                {({ handleBlur, handleChange, values }) => (
                    <Form>
                        <section style={{gridGap: "30px"}} className="mb-5 d-flex flex-wrap section-horizontal-spacing-normal">
                            <div class="mb-3 col-lg-5 col-12">
                                <label className="home-form-label mb-2">
                                    State <sup><img className="col-lg-2" src={ellipseRed} /></sup>
                                </label>
                                <select className="form-select p-3 home-form-select"
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    name="state"
                                    value={values.state}
                                >
                                    <option value="">-Select State-</option>
                                    {statesDisplay}
                                </select>                           
                            </div>
                            <div class="mb-3 col-lg-5 col-12">
                                <label className="home-form-label mb-2">
                                    LGA <sup><img className="col-lg-2" src={ellipseRed} /></sup>
                                </label>
                                <select class="form-select p-3 home-form-select"
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    name="LGA"
                                    value={values.LGA}
                                    disabled={values.state == "" ? true : false}
                                >
                                    <option value="">-Select LGA-</option>
                                    {LGADisplay(values.state)}
                                </select>                           
                            </div>                            
                        </section>
                        <section className="py-5 home-staff-suppot d-flex flex-wrap align-items-center justify-content-between section-horizontal-spacing-normal">
                            <section className="d-flex flex-column justify-content-center">
                                <h3 className="home-form-staff-support-header">CONTACT STAFF SUPPORT ON</h3>
                                <p className="home-form-staff-support-text">Is your laboratory recognized? Make it known today</p>
                            </section>
                            <section className="d-lg-flex flex-wrap justify-content-around align-items-center">
                                <p className="home-form-staff-support-phoneNo mx-lg-5 mx-0 px-lg-4 px-0"><span><img src={phoneWhite} /></span> +234 803 089 4698</p>
                                <button className="mx-lg-5 mx-0 home-form-staff-support-btn home-form-staff-support-btn-text py-3 px-5">Register Laboratory</button>
                            </section>
                        </section>
                    </Form>
                )}
            </Formik>
        </article>
    )
}