import React, { useState } from "react";
import Navigation from "../globals/Navigation";
import loginImg from '../../assests/loginImg.png'
import { Formik, Form, Field } from 'formik'
import * as yup from 'yup'
import InputGroup from 'react-bootstrap/InputGroup';
import { EyeFill, EyeSlashFill } from "react-bootstrap-icons";

export default function Login({ navigation }){

    const [passwordVisibility, setPasswordVisibility] = useState(false)

    const togglePasswordVisibility = () => setPasswordVisibility(!passwordVisibility)

    const toggleShowPassword = () => {
        if (passwordVisibility == true){
            document.querySelector("#password").setAttribute('type', 'text')
        } else{
            document.querySelector("#password").setAttribute('type', 'password')
        }
        togglePasswordVisibility()
    }

    // const schema = yup.object.shape({
    //     email: yup.string().email('Must be a valid email address').required('Email field is required!'),
    //     password: yup.string().required('Password is required')
    // })
    
    return(
        <article className="login-container">
            <Navigation navigation={navigation} />
            <article className="d-flex justify-content-md-center justify-content-center">
                <section className="col-lg-5 p-lg-5 py-lg-5 py-5">
                    <Formik
                        initialValues={{
                            email: "", password: ""
                        }}
                        // validationSchema={schema}
                        onSubmit={( values, { resetForm }) => {
                            // submit to login api
                        }}
                    >
                        {({ handleSubmit, handleChange, handleBlur, values }) => (
                            <Form onSubmit={handleSubmit} className="py-5 mx-lg-5 mx-0">
                                <div className="mb-3 pb-4">
                                    <h1 className="login-header text-center">
                                        Sign in
                                    </h1>
                                    <p className="login-sub-header text-center">
                                        Don't have an account ?
                                        <span className="login-sub-header-signup"> Sign Up</span>
                                    </p>                                    
                                </div>
                                <div class="form-floating mb-5">
                                    <Field 
                                        type="email" 
                                        name="email"
                                        value={values.email}
                                        className="form-control login-input" 
                                        id="email address" 
                                        placeholder="name@example.com"
                                    />
                                    <label for="email address" className="pb-5">Email address</label>
                                </div>
                                <InputGroup className="form-floating">
                                    <Field
                                        type="password"
                                        id="password" 
                                        value={values.password}
                                        name="password"
                                        placeholder="Password"
                                        className="form-control login-input pb-3"
                                    />
                                    <InputGroup.Text onClick={toggleShowPassword} className="bg-transparent login-eye-container" id="basic-addon2">
                                        {
                                            passwordVisibility ? <EyeSlashFill color="#4AFF00" /> : <EyeFill color="#4AFF00" />
                                        }
                                    </InputGroup.Text>
                                    <label for="password" className="pb-5">Password</label>
                                </InputGroup>
                                <p className="login-forgot-password mx-2 mb-5">Forgot password ?</p>
                                <div className="text-center">
                                    <button className="login-signup-btn px-5 py-2 login-signup-btn-text">Sign In</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </section>
                <section className="col-lg-7 d-lg-block d-none d-md-none login-image">
                </section>
            </article>        
        </article>
    )
}