import React from "react";
import dashboardIcon1 from '../../assests/dashboardIcon1.svg'
import dashboardIcon2 from '../../assests/dashboardIcon2.svg'
import dashboardIcon3 from '../../assests/dashboardIcon3.svg'
import dashboardIcon4 from '../../assests/dashboardIcon4.svg'

export default function DashMain(){

    const dashboardItems = [
        {
            icon: dashboardIcon1,
            header: 'Total Number Of Approved Laboratory',
            count: 2,
            text: 'This is the number of completed application that have been approved.',
            backgroundColor: "#00C3FF"
        },
        {
            icon: dashboardIcon2,
            header: 'Total Number Of Pending Application',
            count: 4,
            text: 'This is un-completed application that has to be completed by the laboratory.',
            backgroundColor: "#FF8A00"
        },
        {
            icon: dashboardIcon3,
            header: 'Total Number Of Completed Application',
            count: 0,
            text: 'Displays applications that have been completed but not necessary approved',
            backgroundColor: "#4AFF00"
        }, 
        {
            icon: dashboardIcon4,
            header: 'Total Number Of Un-Approved Application',
            count: 6,
            text: 'comprises of completed and un-completed application',
            backgroundColor: "#5200FF"
        },                        
    ]    

    const dashboardItemsDisplay = 
    dashboardItems.map((dItem, i) => {
        const { icon, header, count, text, backgroundColor } = dItem
        return (
            <div style={{
                backgroundColor,
                marginRight: '2.5vw',
            }} key={i} className="col-lg-5 mx-2 mx-lg-0 dashboard-item-container px-3 py-4">
                <div className="d-flex align-items-start">
                    <img src={icon} /><h3 className="dashboard-item-header mx-2">{header}</h3>
                </div>
                <div className="mx-5 px-1">
                    <h3 className="dashboard-item-count">{count}</h3>
                    <h3 className="dashboard-item-text">{text}</h3>
                </div>
            </div>
        )
    })


    return(
        <article>
            <div className="d-flex flex-wrap" style={{gridGap: "100px"}}>
                {dashboardItemsDisplay}
            </div>
        </article>
    )
}