import React, { useEffect, useState } from "react";
import Navigation from "../globals/Navigation";
import labIconActive from '../../assests/labIconActive.svg'
import homeIconInactive from '../../assests/homeIconInactive.svg'
import dashboardIconInactive from '../../assests/dashboardIconInactive.svg'
import logoutIconInactive from '../../assests/logoutIconInactive.svg'
import profileIconInactive from '../../assests/profileIconInactive.svg'
import walletIconInactive from '../../assests/walletIconInactive.svg'
import labIconInactive from '../../assests/labIconInactive.svg'
import homeIconActive from '../../assests/homeIconActive.svg'
import dashboardIconActive from '../../assests/dashboardIconActive.svg'
import logoutIconActive from '../../assests/logoutIconActive.svg'
import profileIconActive from '../../assests/profileIconActive.svg'
import walletIconActive from '../../assests/walletIconActive.svg'
import { Routes, Route, useNavigate, useLocation } from "react-router-dom";
import DashMain from "./DashMain";
import { Offcanvas } from "react-bootstrap";
import { ArrowBarRight } from "react-bootstrap-icons";

export default function DashboardNav({ navigation }){
    const { pathname } = useLocation()
    const dashboardNavigation = useNavigate()

    const dashGoTo = (path, state) => path == '' ? dashboardNavigation('') : dashboardNavigation(`/v2/${path}`)

    const sideNavs = [
        {
            inActive: dashboardIconInactive,
            active: dashboardIconActive,
            path: '',
            pathId: ''
        },
        {
            inActive: homeIconInactive,
            active: homeIconActive,
            path: 'dashboard/home',
            pathId: 'home'
        }, 
        {
            inActive: labIconInactive,
            active: labIconActive,
            path: 'dashboard/laboratory',
            pathId: 'laboratory'
        },
        {
            inActive: walletIconInactive,
            active: walletIconActive,
            path: 'dashboard/wallet',
            pathId: 'wallet'
        },
        {
            inActive: profileIconInactive,
            active: profileIconActive,
            path: 'dashboard/profile',
            pathId: 'profile'
        },
        {
            inActive: logoutIconInactive,
            active: logoutIconActive,
            path: 'dashboard/logout',
            pathId: "logout"
        }
    ]

    const sideNavsDisplay = 
        sideNavs.map((sideNav, i) => {
            const { active, inActive, path, pathId } = sideNav
            const isActive = 
                <div onClick={() => dashGoTo(path)} key={i} className="col-4 col-md-5 d-flex align-items-start d-lg-block mb-lg-5 mb-2 pb-lg-3 pb-0 dashboard-side-nav-icon">
                    <img className="col-lg-8 col-2" src={active} />
                </div>

            if(pathname == `/v2/dashboard/${pathId}`){
                return isActive
            } else{
                if(pathname == `/v2/dashboard` && pathId == ''){
                    return isActive
                }
                return (
                    <div onClick={() => dashGoTo(path)} key={i} className="d-flex align-items-start col-4 col-md-5 mb-lg-5 pb-lg-3 mb-2 pb-0 dashboard-side-nav-icon">
                        <img className="col-lg-8 col-2" src={inActive} />
                    </div>
                )                
            }
        })
    
    return(
        <article>
            <div className="d-lg-block d-none">
                <Navigation navigation={navigation} isDashboard={true} />
            </div>
            <div className="d-block d-lg-none">
                <Navigation navigation={navigation} />
            </div>            
            <section className="d-none dashboard-side-nav d-lg-flex flex-column justify-content-center align-items-center text-center position-fixed">
                {sideNavsDisplay}
            </section> 
            <section className="px-3 px-md-5 d-flex mb-4 d-lg-none align-items-center dashboard-nav-small-screen">
                {sideNavsDisplay}                 
            </section> 
            <section className="dashboard-body mb-5 pb-3">
                <Routes>
                    <Route path="/" element={<DashMain />} />
                    <Route path="home" element={<div>Home</div>} />
                    <Route path="laboratory" element={<div>Laboratory</div>} />
                    <Route path="wallet" element={<div>Wallet</div>} />
                    <Route path="profile" element={<div>Profile</div>} />
                </Routes>
            </section>                      
        </article>
    )
}