import React, { useEffect, useState } from "react";
import logo from '../../assests/logo.svg'
import { useLocation } from "react-router-dom";
import { Offcanvas } from 'react-bootstrap'
import { MenuButtonWideFill } from 'react-bootstrap-icons'

export default function Navigation({ navigation, isDashboard }){
    const { pathname } = useLocation()
    const [canvasNav, setCanvasNav] = useState(false)

    const hideCanvasNav = () => setCanvasNav(false)
    const showCanvasNav = () => setCanvasNav(true)

    const navLinks = [
        {
            navName: 'Home',
            navId: "homeNav",
            route: '/'
        },
        {
            navName: 'Dashboard',
            navId: "dashboardNav",
            route: 'dashboard'
        },
        {
            navName: 'Lab Service',
            navId: "labServiceNav",
            route: 'lab-service'
        },
        {
            navName: 'Contact',
            navId: "contactNav",
            route: 'contact'
        },
        {
            navName: 'Login',
            navId: "loginNav",
            route: 'login'
        },                                
    ]

    const navClick = (e, id, route) => {
        setCanvasNav(false)
        const navs = document.querySelectorAll('.isActive')
        for(let i = 0; i < navs.length; i++){
            navs[i].classList.add('inactiveNav')
        }
        document.querySelector(`#${id}`).classList.remove('inactiveNav')
        document.querySelector(`#${id}`).classList.add('activeNav')

        navigation(route)
    }

    const navsDisplay =
        navLinks.map((navLink, i) => {
            const { navName, navId, route } = navLink
            return (
                <div key={i} className="mb-lg-0 mb-4" onClick={e => navClick(e, navId, route)}>
                    <div className="main-nav-text text-lg-center text-start">{navName}</div>
                    <div id={navId} className="isActive col-lg-auto col-3"></div>
                </div>
            )
        })

    useEffect(() => {
        if(pathname == '/v2'){
            document.querySelector(`#homeNav`).classList.add('activeNav')
        } else if(pathname == '/v2/dashboard'){
            document.querySelector('#dashboardNav').classList.add('activeNav')
        } else if(pathname == '/v2/login'){
            document.querySelector('#loginNav').classList.add('activeNav')
        }
    }, [pathname])

    return(
        <article className="navigation-container fixed-top d-flex justify-content-between align-items-center px-lg-5 px-md-5 py-2 px-3 py-lg-0 pb-lg-1">
            <section className="col-lg-5 col-6 col-md-6">
                <img src={logo} className={`
                    col-lg-2 col-3 col-md-2 ${isDashboard && 'dashboard-main-nav'}
                `} />
            </section>
            <section className="d-none col-0 col-lg-6 d-lg-flex justify-content-around">
                {navsDisplay}
            </section>
            <section className="d-lg-none d-block px-2 px-md-0 col-6 col-md-6 text-end">
                <MenuButtonWideFill onClick={showCanvasNav} size={32} color="#4AFF00" />
                <Offcanvas placement="end" show={canvasNav} onHide={hideCanvasNav}>
                    <Offcanvas.Header closeButton>
                        <Offcanvas.Title>
                        <section className="col-12 col-md-12">
                            <img src={logo} className="col-5 col-md-4" />
                        </section>                            
                        </Offcanvas.Title>
                    </Offcanvas.Header>
                    <Offcanvas.Body>
                        {navsDisplay}
                    </Offcanvas.Body>
                </Offcanvas>                
            </section>
        </article>
    )
}