import React from "react";
import { Route, Routes, useNavigate } from "react-router-dom";
import App from "./App";
import Login from "./components/auth/Login";
import DashboardNav from "./components/dashboard/DashboardNav";

export default function Routing(){

    const navigate = useNavigate()

    const navigation = (path, state) => path == '/' ? navigate('/') : navigate(`/v2/${path}`, state)

    return (
        <Routes>
            <Route path="/v2" element={<App navigation={navigation} />} />

            <Route path="https://c-genesis.github.io/v2/login" element={<Login navigation={navigation} />} />

            <Route path="https://c-genesis.github.io/v2/dashboard/*" element={<DashboardNav navigation={navigation} />} />

            <Route path="https://c-genesis.github.io/v2/lab-service" element={<h1>Lab Service</h1>} />
            
            <Route path="https://c-genesis.github.io/v2/contact" element={<h1>Contact</h1>} />

            <Route path="*" element={
                <div className="d-flex justify-content center my-3">
                    <h1>Page Not Found!</h1>
                </div>
            }/>
        </Routes>
    )
}